<template>
    <div>
        <transition name="fade">
            <loading v-show="loading"></loading>
        </transition>
        
        <!--标题栏-->
        <div class="titlebar">{{musics.getCurrentMusicField('name')}}</div>

        <!--唱片机-->
        <div class="player" :class="{on: musicPlaying}">
            <!--拾音器-->
            <div class="player__pickups">
                <div class="player__pickups__axis"></div>
                <div class="player__pickups__pole"></div>
            </div>
            <!--唱片-->
            <div class="player__disc">
                <!--唱片封面-->
                <div class="player__disc__cover">
                    <img :src="require('@/assets/imgs/cover_default.jpg')">
                </div>
            </div>
            <!--李志LOGO-->
            <img :src="require('@/assets/imgs/logo_lizi.png')" class="player__logo" alt="logo">
            <!--音频播放器-->
            <audio id="player_audio" :src="musics.getCurrentMusicSrc()"></audio>
        </div>

        <!-- 播放控制 -->
        <div class="control-bar">
            <div class="control-bar__btn">
                <van-icon class-prefix="iconfont" :name="playModeIcon" @click="handlePlayModeChange" />
            </div>
            <div class="control-bar__btn closer">
                <van-icon class-prefix="iconfont" name="previous" @click="playBySwitcher('prev')" />
            </div>
            <div class="control-bar__btn large">
                <van-icon class-prefix="iconfont" :name="musicPlaying ? 'pause' : 'play'" @click="togglePlay" />
            </div>
            <div class="control-bar__btn closer">
                <van-icon class-prefix="iconfont" name="next" @click="playBySwitcher('next')" />
            </div>
            <div class="control-bar__btn">
                <van-icon class-prefix="iconfont" name="list" @click="togglePlayList" />
            </div>
        </div>

        <!--播放列表-->
        <transition name="fade">
            <div class="mask" v-show="playListVisible" @click.self="closePlayList">
                <div class="playlist">
                    <div class="playlist__head">
                        <h4 class="playlist__title">播放列表</h4>
                        <div class="playlist__btnclose" @click="closePlayList">
                            <van-icon name="cross" />
                        </div>
                    </div>
                    <div class="playlist__body">
                        <div class="playlist__body__list">
                            <div class="list__item" v-for="(music, index) in musics.musics" :key="index"
                                :class="{active: music === musics.currentMusic}"
                                @click="playByPlayList(music)">
                                <van-icon class-prefix="iconfont" name="play" />
                                <span class="item__name">{{music.name}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import {Musics} from '../musics';
import musicHelper from '../helper/MusicHelper.js';
import {getAudioPlayer} from '../helper/OfomHelper.js';
import loading from '@/components/loading.vue'
export default {
    components: {
        loading
    },
    data() {
        return {
            musics: new Musics({}),
            audioPlayer: undefined,
            playListVisible: false,
            musicPlaying: false,
            playModeIcon: 'playlist',
            loading: true
        }
    },
    created() {
        musicHelper.findAll().then(musicContent => {
            const rawId = window.localStorage['CURRENT_MUSICS_ID'];
            const id = rawId ? Number(rawId) : 0;

            this.musics = new Musics(musicContent, 'ALL_REPEAT', musicHelper.findById(id));
            this.audioPlayer = getAudioPlayer(this.musics);

            //故意让加载动画显示的时间更久，避免一闪而过观感不好
            setTimeout(() => this.loading = false, 800);
        });
    },
    methods: {
        //播放列表开关
        togglePlayList() {
            this.playListVisible = !this.playListVisible
        },
        //关闭播放列表
        closePlayList() {
            this.playListVisible = false;
        },
        //播放音乐
        playMusic(playAction, musicsAction, nextPlayingStatus, delayTime = 300) {
            setTimeout(() => playAction(), delayTime)
            musicsAction();
    
            this.musicPlaying = nextPlayingStatus;
            this.closePlayList();
            window.localStorage['CURRENT_MUSICS_ID'] = this.musics.currentMusic.id;
        },
        //播放控制
        togglePlay() {
            const playerAction = ({
                true: () => this.audioPlayer.pause(),
                false: () => this.audioPlayer.play()
            })[this.musicPlaying];

            this.playMusic(() => playerAction(), _.identity, !this.musicPlaying);
        },
        //播放列表切歌
        playByPlayList(music) {
            if (music === this.musics.currentMusic) {
                return;
            }
            
            this.playMusic(() => this.audioPlayer.play(), () => this.musics.play(music), true);
        },
        //控制器切歌
        playBySwitcher(action) {
            const musicsAction = {
                'next': () => this.musics.next(),
                'prev': () => this.musics.prev()
            }[action];

            this.playMusic(() => this.audioPlayer.play(), () => musicsAction() , true);
        },
        playMusic(playAction, musicsAction, nextPlayingStatus, delayTime = 300) {
            setTimeout(() => playAction(), delayTime)
            musicsAction();
    
            this.musicPlaying = nextPlayingStatus;
            this.closePlayList();
            window.localStorage['CURRENT_MUSICS_ID'] = this.musics.currentMusic.id;
        },
        handlePlayModeChange(){
            const nextMode = this.musics.nextPlayMode();
            this.playModeIcon = {
                ALL_REPEAT: 'playlist',
                SHUFFLE: 'random',
                REPEAT_ONCE: 'single'
            }[nextMode];
        }
    }
}
</script>

<style lang="less" scoped>

</style>