import musicHelper from '../helper/MusicHelper.js';
var _ = require('lodash');

export class Musics {

    musics;

    currentMusic;

    switcher;

    playMode= 'ALL_REPEAT';

    constructor(musics, playMode='ALL_REPEAT', currentMusic) {
        this.musics = musics;
        this.playMode = playMode;
        this.switcher = SWITCHER_CLASS_HOLDER[playMode](this);
        this.currentMusic = currentMusic || _.head(musics);
    } 

    nextPlayMode() {
        const nextPlayMode = {
            ALL_REPEAT: 'SHUFFLE',
            SHUFFLE: 'REPEAT_ONCE',
            REPEAT_ONCE: 'ALL_REPEAT'
        }[this.playMode];

        this.playMode = nextPlayMode;
        this.switcher = SWITCHER_CLASS_HOLDER[this.playMode](this);
        return this.playMode;
    }

    onEnded() {
        this.switcher.onEnded();
    }

    prev() {
        this.switcher.prev();
    }

    next() {
        this.switcher.next();
    }

    play(music) {
        this.currentMusic = music;
    }

    getCurrentMusicField(field){
        if(!this.currentMusic){
            return;
        }
        return this.currentMusic[field];
    }

    getCurrentMusicSrc() {
        if(!this.currentMusic){
            return;
        }
        return 'http://halve.gitee.io/assets/musics/' + this.currentMusic['name'] + '.mp3';
    }
}

export class Music {
    
    id;

    name;

    cover;

    src;

    constructor(id, name, cover, src){
        this.id = id;
        this.name = name;
        this.cover = cover;
        this.src = src;
    };
}

class BaseSwitcher {

    musics;

    constructor (musics) {
        this.musics = musics;
    }

    onEnded () {
        throw Error('must implement in subclass');
    }

    next() {
        throw Error('must implement in subclass');
    }

    prev() {
        throw Error('must implement in subclass');
    }
}

//列表循环
class AllRepeatSwitcher extends BaseSwitcher {

    onEnded () {
        this.next();
    }

    next() {
        const currentIndex = musicHelper.findIndexByParam(this.musics.currentMusic);
        const maxIndex = musicHelper.count() - 1;
        this.musics.currentMusic = musicHelper.findByIndex(currentIndex === maxIndex ? 0 : currentIndex + 1);
    };

    prev() {
        const currentIndex = musicHelper.findIndexByParam(this.musics.currentMusic);
        const maxIndex = musicHelper.count() - 1;
        this.musics.currentMusic = musicHelper.findByIndex(currentIndex === 0 ? maxIndex : currentIndex - 1);
    }
}

//随机播放
class ShuffleSwitcher extends BaseSwitcher {

    onEnded () {
        this.next();
    }

    next() {
        this.musics.currentMusic = musicHelper.findByIndex(_.random(musicHelper.count() - 1))
    };

    prev() {
        this.next()
    }
}

//单曲循环
class RepeatOnceSwitcher extends BaseSwitcher {

    onEnded () {
        
    }

    next() {
        const currentIndex = musicHelper.findIndexByParam(this.musics.currentMusic);
        const maxIndex = musicHelper.count() - 1;
        this.musics.currentMusic = musicHelper.findByIndex(currentIndex === maxIndex ? 0 : currentIndex + 1);
    };

    prev() {
        const currentIndex = musicHelper.findIndexByParam(this.musics.currentMusic);
        const maxIndex = musicHelper.count() - 1;
        this.musics.currentMusic = musicHelper.findByIndex(currentIndex === 0 ? maxIndex : currentIndex - 1);
    }
}

const SWITCHER_CLASS_HOLDER = {
    ALL_REPEAT: musics => new AllRepeatSwitcher(musics),
    SHUFFLE: musics => new ShuffleSwitcher(musics),
    REPEAT_ONCE: musics => new RepeatOnceSwitcher(musics),
}